body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.spotify img {
  position: absolute;
  left: -120px;
  opacity: 0.4;
  height: 100%;
  max-width: 100%;  
}

.create-requests {
  overflow: hidden;
  padding-left: 80px;
}

.song-with-album img {
  position: absolute;
  right:0px;
  opacity:1;
  height:100%;
  max-width:100%
}

.song-with-album p, span {
  max-width: 60%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.msg-container {
  text-align: center;
}
.msg {
  max-width:50%;
  border: 1px solid;
  padding:25px;
  color: grey;
  display: inline-block;
}
.msg-error {
  border-color: #d32f2f;
  background-color: #ef5350;
  color: white;
}

.notifications {
  white-space: initial;
}

.break-line {
  white-space: initial;
}

hr {
  border: 2px solid green;
  border-radius: 1px
}

.srButton {
  text-align: center;
  margin-top: 15px;
}

.wrapper {
  position: relative;
  display: inline-block;
}
.close:before {
  content: 'âœ–';
}
.close {
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
}

